@import url(https://fonts.googleapis.com/css2?family=Jost:wght@400;700&display=swap);
.calcItem{width:350px;font-size:1.1rem;margin:0 auto}.calcItem:after{content:' \20BD'}

.modal{position:fixed;top:0;right:0;bottom:0;left:0;background:rgba(0,0,0,0.8);z-index:99999;transition:opacity 400ms ease-in}.modal__container{border:2px solid black;padding:35px;background-color:#FFFFFF;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.modal__close{position:absolute;top:23px;right:15px;width:20px;height:20px}.modal__close:hover{cursor:pointer}.modal__close:before,.modal__close:after{content:'';background-color:#000000;height:2px;width:20px;position:absolute}.modal__close:before{transform:rotate(45deg)}.modal__close:after{transform:rotate(-45deg)}

footer{background-color:#000000;display:flex;justify-content:center;align-items:center}.social-link{padding:10px}.social-link img{width:20px}

.nav{background-color:#000000;display:flex;justify-content:center;align-items:center;width:100vw;height:42px;position:fixed;top:0}.nav ul li{display:inline-block}.nav ul li a{text-decoration:none;color:#FFFFFF;text-transform:uppercase;padding:7px 30px}.nav ul li a:before{content:'';margin-left:-30px;position:absolute;bottom:0;width:0;background-color:#FFFFFF;transition:width .25s linear}.nav ul li a:hover:before{width:134px;height:2px}.active{border-bottom:2px solid #FFFFFF}.mainContainer{width:100vw;height:100vh;display:flex;flex-direction:column;justify-content:center}.content{height:100%;display:flex;align-items:center;justify-content:center;margin:0 auto}@media screen and (min-width: 1080px){.content{width:40vw}}

.wrapper{width:100%;margin:0px 50px}.wrapper h1{text-align:center;margin-bottom:50px}.form{display:flex;flex-direction:column}.form label{text-align:center;padding:10px 0px;font-size:1.5rem}.form input,.form select{font-size:1.2rem;border-radius:5px;padding:5px;border:none;box-shadow:0px 0px 5px #000}.form button{border:none;font-size:1.2rem;padding:10px 30px;margin:30px auto 30px auto;width:200px;border-radius:5px;box-shadow:0px 0px 5px 1px #000;background-color:#F45B69;outline:none}.form button:active{background-color:#b03838;transform:translate3d(0, 0, 5px)}.creditTime__select{display:flex;justify-content:space-between}.creditTime__select #creditTime{width:82%}@media screen and (max-width: 480px){.form input{font-size:.754rem;padding:10px}}*,::before,::after{margin:0;padding:0;box-sizing:border-box}body{font-family:Jost, sans-serif;height:100vh;width:100vh;display:flex;background:linear-gradient(0deg, #e0a112, #ebd911);background-size:400% 400%;-webkit-animation:bgAnimate 10s ease infinite;animation:bgAnimate 10s ease infinite}@-webkit-keyframes bgAnimate{0%{background-position:51% 0%}50%{background-position:50% 100%}100%{background-position:51% 0%}}@keyframes bgAnimate{0%{background-position:51% 0%}50%{background-position:50% 100%}100%{background-position:51% 0%}}

